html {
  font-size: 10px;
}

body {
  background: #ffc600;
  font-family: helvetica neue;
  font-size: 20px;
  font-weight: 200;
}

.words {
  max-width: 500px;
  background: -webkit-gradient(linear, 0 0, 0 100%, from(#d9eaf3), color-stop(.04, #fff)) 0 4px / 100% 3rem;
  border-radius: 5px;
  margin: 50px auto;
  padding: 1rem 2rem 1rem 5rem;
  line-height: 3rem;
  position: relative;
  box-shadow: 10px 10px rgba(0, 0, 0, .1);
}

p {
  margin: 0 0 3rem;
}

.words:before {
  content: "";
  width: 4px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0) #efe4e4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 30px;
}

